import React from 'react';
import { Link, useStaticQuery, graphql } from 'gatsby';
import styled from 'styled-components';
import {
  Twitter,
  LinkedIn,
  NightsStay,
  Brightness7,
  GitHub,
} from '@material-ui/icons';
import { Zoom } from '@material-ui/core';
import { ThemeToggler } from 'gatsby-plugin-dark-mode';

import { rhythm, scale } from '../utils/typography';
import { from, Breakpoints } from '../utils/theme';

const Wrapper = styled.header`
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-bottom: ${rhythm(1)};

  ${from(Breakpoints.MD, `margin-bottom: ${rhythm(2)};`)}
`;

const Brand = styled(Link)`
  ${scale(0.5)}
  box-shadow: none;
  font-family: Montserrat, sans-serif;
  font-weight: 900;
  color: var(--textNormal);
  text-decoration: none;
  max-width: 40px;
  overflow: hidden;
  white-space: nowrap;

  &:hover {
    text-decoration: none;
  }

  ${from(
    Breakpoints.MD,
    'max-width: none; overflow: auto; white-space: initial;'
  )}
`;

const Box = styled.span`
  background-color: var(--bg-inverted);
  color: var(--text-normal-inverted);
  border-radius: 5px;
  display: inline-block;
  padding: 0 ${rhythm(0.2)};
  margin: 0 ${rhythm(0.1)};
  min-width: 40px;
  text-align: center;
  transition: background-color 0.25s linear, color 0.25s linear;

  &:first-child {
    margin-left: 0;
  }
`;

const Tools = styled.div`
  margin-left: auto;
`;

const IconLink = styled.a`
  color: var(--icon);
  box-shadow: none;
  text-decoration: none;
  margin: 0 ${rhythm(0.2)};
  transition: color 0.3s ease-in;

  &:hover {
    text-decoration: none;
    color: var(--icon-hover);
  }
`;

const IconButton = styled.button`
  color: var(--icon);
  cursor: pointer;
  background-color: transparent;
  border: 0;
  margin: 0 ${rhythm(0.2)};
  padding: 0;
  transition: color 0.3s ease-in;

  &:hover {
    color: var(--icon-hover);
  }
`;

const Header: React.FC = () => {
  const data = useStaticQuery(graphql`
    query SocialQuery {
      site {
        siteMetadata {
          social {
            twitter
            linkedin
            github
          }
        }
      }
    }
  `);

  const { twitter, linkedin, github } = data.site.siteMetadata.social;

  return (
    <Wrapper>
      <Brand to="/">
        <Box>j</Box>azz<Box>p</Box>eh
      </Brand>
      <Tools>
        {linkedin && (
          <IconLink
            href={`https://www.linkedin.com/in/${linkedin}/`}
            target="_blank"
          >
            <LinkedIn fontSize="large" />
          </IconLink>
        )}
        {github && (
          <IconLink href={`https://github.com/${github}`} target="_blank">
            <GitHub fontSize="large" />
          </IconLink>
        )}
        {twitter && (
          <IconLink href={`https://twitter.com/${twitter}`} target="_blank">
            <Twitter fontSize="large" />
          </IconLink>
        )}
        <ThemeToggler>
          {({ theme, toggleTheme }) => (
            <IconButton
              onClick={() => toggleTheme(theme === 'light' ? 'dark' : 'light')}
            >
              {theme === 'light' && <NightsStay fontSize="large" />}
              {theme !== 'light' && <Brightness7 fontSize="large" />}
            </IconButton>
          )}
        </ThemeToggler>
      </Tools>
    </Wrapper>
  );
};

export default Header;
