import { createGlobalStyle } from 'styled-components';
import { rhythm } from './typography';

export enum Breakpoints {
  S = 576,
  MD = 768,
  LG = 992,
  XL = 1200,
}

export const GlobalStyle = createGlobalStyle`
  body {
    --bg: white;
    --bg-inverted: black;
    --text-normal: hsla(0,0%,0%,0.9);
    --text-normal-inverted: white;
    --text-quote: hsla(0,0%,0%,0.59);
    --text-eyebrow: #424242;
    --icon: #7d7d7d;
    --icon-hover: black;
    --post-bg: white;
    --link: #007acc;
    --divider: hsla(0,0%,0%,0.2);
    --table-border: hsla(0,0%,0%,0.12);
    --pre-bg: #fdf6e5;
    --pre-text: #424242;

    background-color: var(--bg);
    color: var(--textNormal);
  }

  body.dark {
    --bg: #121318;
    --bg-inverted: white;
    --text-normal: white;
    --text-normal-inverted: black;
    --text-quote: #ccc;
    --text-eyebrow: #ccc;
    --icon: #7d7d7d;
    --icon-hover: white;
    --post-bg: #303030;
    --link: #61dafb;
    --divider: #ccc;
    --table-border: #454d55;
    --pre-bg: #1e1e1e;
    --pre-text: #d4d4d4;

    background-color: var(--bg);
    color: var(--text-normal);
  }

  body {
    transition: background-color 0.25s linear, color 0.25s linear;
  }

  a {
    color: var(--link);
  }

  blockquote {
    font-size: ${rhythm(0.7)};
    color: var(--text-normal);
    border-left-color: var(--text-normal);

    code {
      font-size: ${rhythm(0.7)};
    }
  }

  ul {
    margin-left: 1.75rem;
  }

  hr {
    background-color: var(--divider);
  }

  table {
    border-top: 1px solid var(--table-border);

    th, td {
      color: var(--text-normal);
      padding: ${rhythm(0.3)};
      border: 0;
      border-bottom: 1px solid var(--table-border);

      p {
        margin: 0;
      }
    }
  }

  .math {
    border-radius: var(--grvsc-border-radius, 8px);
    background-color: var(--pre-bg);
    color: var(--pre-text);
    font-size: ${rhythm(1)};
    margin-bottom: ${rhythm(1)};
    padding-top: var(--grvsc-padding-top, var(--grvsc-padding-v, 1rem));
    padding-bottom: var(--grvsc-padding-bottom, var(--grvsc-padding-v, 1rem));
    padding-left: var(--grvsc-padding-left, var(--grvsc-padding-h, 1.5rem));
    padding-right: var(--grvsc-padding-right, var(--grvsc-padding-h, 1.5rem));
    text-align: center;
  }

  .text {
    &-info {
      color: #17a2b8;
    }

    &-success {
      color: #28a745;
    }

    &-warning {
      color: #ffc107;
    }

    &-danger {
      color: #dc3545;
    }
  }
`;

export const from = (breakpoint: Breakpoints, content: string): string => {
  return `
    @media screen and (min-width: ${breakpoint}px) {
      ${content}
    }
  `;
};

export const upto = (breakpoint: Breakpoints, content: string): string => {
  return `
    @media screen and (max-width: ${breakpoint}px) {
      ${content}
    }
  `;
};

export const between = (
  from: Breakpoints,
  to: Breakpoints,
  content: string
): string => {
  return `
    @media only screen and (max-width: ${to}x) and (min-width: ${from}px) {
      ${content}
    }
  `;
};
